<!-- @format -->

<template>
	<apexchart width="550" type="bar" :options="chartOptions" :series="series"></apexchart>
</template>

<script>
// this is where the line chart sparkline gets its colors
	import { genericApi } from "../../plugins/axios";
	export default {
		name: 'ApexBarChart',
		data() {
			return {
				series: [{
					data: [	]
				}],
				chartOptions: {
					chart: {
					type: 'bar',
					height: 350
					},
					plotOptions: {
						bar: {
							borderRadius: 4,
							horizontal: true,
						}
					},
                    title: {
						text: 'Yearly Results',
						offsetX: 15,
						style: {
							color: '#fff'
						}
					},
					subtitle: {
						text: '(Click on bar to see details)',
						offsetX: 15,
						style: {
							color: '#fff'
						}
					},
					dataLabels: {
						enabled: true,
						textAnchor: 'start',
						style: {
							colors: ['#fff']
						},
					},
                    yaxis: {
						labels: {
							show: true,
							 style: {
								color: '#fff',
								fontSize: '12px',
								cssClass: 'apexcharts-yaxis-label',
							},
						},
					},
					xaxis: {
						categories: [],
						labels: {
							show: true,
							style: {
								colors: '#fff',
								fontSize: '12px',
							}
						}
					}
				},
			}
		},
		created(){
			this.getAllStatus();
		},
		methods: {
			
			getAllStatus(){
				//uses axios to send get request to api in genericAPI
				genericApi
				.get('type/status')
				.then((response) => {
					let type=[];
					let count=[];
					let data = response.data;
					
					data.forEach(item=>{
						console.log(item)
						count.push(item['count'])
						type.push(item['type'])
					})
					this.series= [{
						data: count
					}]
					console.log(type)
					this.chartOptions.xaxis={categories :type};
					this.$set(this.chartOptions,'xaxis.categories',type)
					this.$forceUpdate();
				})
				.catch((error) => console.log(error));
		
			}
		}
	}
</script>