<!--  
	Entrance File
	Hint: This is set up to have two completely different website layouts 
	depending on if you are logged in or not , 
	so that is what ExternalView and DashboardView are for. LoginView renders in Externalview
-->
<template>
  <transition mode="out-in">
    <!--密碼登入-->
    <DashboardView v-if="secret === true || secret === 'true'" />
    <v-card v-else width="400" class="mx-auto text-center">
      <v-card-text>
        <v-text-field
          label="請入密碼"
          outlined
          hide-details
          :append-icon="showeye ? 'mdi-eye' : 'mdi-eye-off'"
          :type="showeye ? 'text' : 'password'"
          v-model="inputSecret"
          @click:append="showeye = !showeye"
        >
        </v-text-field>
      </v-card-text>
      <v-card-actions>
        <v-container>
          <v-row>
            <v-col md="12">
              <v-btn
                block
                class="white--text"
                color="red"
                @click="checkPassword()"
              >
                送出
              </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-card-actions>
    </v-card>
  </transition>
</template>

<script>
// checks to see if auth jwt token is valid or has expired, if it gets back 401 error log out
import Vue from "vue";
import * as Sentry from "@sentry/vue";
import DashboardView from "./views/DashboardView.vue";

// import ExternalView from './views/ExternalView.vue'
// import LoginView from './views/LoginView.vue'

Sentry.init({
  Vue,
  logErrors: true,
  // Set tracesSampleRate to 1.0 to capture 100%
  // of transactions for performance monitoring.
  // We recommend adjusting this value in production
  tracesSampleRate: 1.0,
});

export default {
  name: "App",
  components: {
    DashboardView,
  },
  data: () => ({
    showeye: false,
    secret: null,
    inputSecret: "", //this.$cookies.get('decathlon')
    error: false,
  }),
  created: function () {
    this.$http.interceptors.response.use(
      (response) => {
        return response;
      },
      (error) => {
        if (error.response.status === 401) {
          if (this.$store.getters.authorized) {
            this.$store.dispatch("refreshtoken");
          } else {
            return Promise.reject(error);
          }
        } else {
          return Promise.reject(error);
        }
      }
    );
  },
  methods: {
    checkPassword() {
      if (this.inputSecret === "53048094") {
        this.secret = true;
        this.$cookies.set("decathlon", true);
        this.$forceUpdate();
      } else {
        console.log("erro");
        this.error = true;
      }
    },
  },
};
</script>

