<template>

	<v-container fluid :style="$vuetify.breakpoint.mdAndUp ? 'width: 1660px' : 'width: 100%' ">

		<v-row class="d-flex align-start justify-center mb-2">

			<!-- Left Side's Filter -->
			<v-col md="7" class="my-2">
				<v-card
					color="primary"
					dark
					>
					<template v-slot:progress>
					<v-progress-linear
						absolute
						color="primary"
						height="4"
						indeterminate
					></v-progress-linear>
					</template>
					<v-form>
						<v-container>
							<v-row>
								<v-col cols='3'>
									<h1>受眾篩選</h1>
									<h2>Filter Audience</h2>
									<v-list>
										<span>目前受眾包</span>
										<v-list-item-group>
											<v-list-item
											v-for="(item, i) in audienceList"
											:key="i"
											>
											<v-list-item-content>
												<v-list-item-title v-text="item"></v-list-item-title>
											</v-list-item-content>
											</v-list-item>
										</v-list-item-group>
									</v-list>
									<v-list>
										<span>分析受眾</span>
										<v-list-item-group>
											<v-list-item
											v-for="(item, i) in analystAudience"
											:key="i"
											>
											<v-list-item-content>
												<v-list-item-title v-text="item['audience_name']"></v-list-item-title>
											</v-list-item-content>
											</v-list-item>
										</v-list-item-group>
									</v-list>
								</v-col>
								<v-col cols='9'>
									<v-alert
									color="primary"
									dark
									border="left"
									prominent
									>
									<small>操作基礎</small>
									<v-list dense color="primary">
										<v-list-item-group
										>
											<v-list-item
											v-for="(item, i) in hint"
											:key="i"
											>
											<v-list-item-icon>
												<v-icon v-text="item.icon"></v-icon>
											</v-list-item-icon>
											<v-list-item-content  class="text-wrap">
												<v-list-item-title v-text="item.text"></v-list-item-title>
											</v-list-item-content>
											</v-list-item>
										</v-list-item-group>
										</v-list>
									</v-alert>
								</v-col>
							</v-row>
							<v-row>
							<v-col cols='12'>
								<v-simple-table dense>
									<thead>
										<tr>
										<th class="text-left" width="190">
											種類 Type
										</th>
										<th class="text-left">
											標籤 Tag
										</th>
										<th class="text-left">
											刪除 Delete
										</th>
										</tr>
									</thead>
									<tbody>
										<tr
										v-for="item,key in list"
										:key="'list-'+key"
										>
											<td>
												<v-icon left dark>
													mdi-filter-outline 
												</v-icon>
												{{ item.type }}
											</td>
											<td>
												<v-chip
												v-for="(list, i) in item.condition"
												:key="i"
												class="ma-1"
												close
												@click:close="remove(i,key)"
												>
												{{ list }}
												</v-chip>
											</td>
											<td>
												<v-icon dark @click="removeCondition(key)">
													mdi-delete
												</v-icon>
											</td>
										</tr>
									</tbody>
								</v-simple-table>
							</v-col>
							</v-row>
							<v-row class='pt-5'>
										
								<v-col cols='12' align="right" class='pa-0 ma-0'>
									<v-divider></v-divider>
								</v-col>
								
							</v-row>
							<v-row>
								<v-col cols="12">
									
									<v-container >
										<v-row>
											<v-col cols="3">
												<v-select
													:items="filterType"
													item-value='name'
													item-text='text'
													v-model="selectType"
													label="篩選類別 Type"
													filled
												></v-select>
											</v-col>
											<v-col col='8'>
												<!-- Transaction Date Formate -->
												<v-menu v-if="selectType ==='transactionDate'"
													v-model="menu2"
													:close-on-content-click="false"
													:nudge-right="40"
													transition="scale-transition"
													offset-y
													min-width="auto"
												>
												
													<template v-slot:activator="{ on, attrs }">
														<v-text-field
															v-model="dateRangeText"
															label="交易時間範圍 Transaction Date's Range"
															prepend-icon="mdi-calendar"
															readonly
															v-bind="attrs"
															v-on="on"
															filled
															hide-details="auto"
														></v-text-field>
													</template>
													<v-date-picker
														v-model="selectCondition"
														range
														@input="menu2 =(selectCondition.length>=2)?false:true"
													></v-date-picker>
												</v-menu>

												<!-- Event Relative -->
												<v-row  v-if="selectType ==='eventRelative'">
													<v-col>
														<v-row>
															<v-col col="12">
																<v-card style="background: rgba(255, 255, 255, 0.08) !important;">
																	<v-card-title>標籤顯示處 Tag Display</v-card-title>
																	<v-card-text>
																		<v-chip
																		class="ma-2"
																		label
																		v-for="(list,index) in selectCondition"
																		:key="'range-'+index"
																		close
																		@click:close="removeSelect(index)">
																		
																		<strong>{{ list }}</strong>&nbsp;
																		</v-chip>
																	</v-card-text>
																</v-card>
															</v-col>
														</v-row>
														
														<!-- Range Selected -->
														<v-row>
															<v-col cols="4">
																<v-select
																	:items="eventType"
																	item-value='name'
																	item-text='text'
																	label="篩選類別 Type"
																	filled
																	v-model="eventTypeSelect"
																></v-select>
															</v-col>
															<v-col cols="7" v-if="eventDetailType[0].name ==='event_sport' ">
																
																<v-select
																	v-if="eventSport !==null"
																	:items="eventSport"
																	item-value='name'
																	item-text='text'
																	filled
																	v-model="filterCondition"
																></v-select>
																<span v-else> 資料撈取中請稍後 Please wait for search data</span>
																
																	
															</v-col>
															<v-col cols="7" v-else-if="eventDetailType[0].name ==='event_level' ">
																
																<v-select
																	:items="eventLevel"
																	item-value='name'
																	item-text='text'
																	filled
																	v-model="filterCondition"
																></v-select>
																	
															</v-col>
															<v-col cols="7" v-else>
																<!-- Date Formate -->
																<v-menu
																	v-model="menu2"
																	:close-on-content-click="false"
																	:nudge-right="40"
																	transition="scale-transition"
																	offset-y
																	min-width="auto"
																>
																
																	<template v-slot:activator="{ on, attrs }">
																		<v-text-field
																			v-model="filterDateRange"
																			label="時間範圍 Event Date's Range"
																			prepend-icon="mdi-calendar"
																			readonly
																			v-bind="attrs"
																			v-on="on"
																			filled
																			hide-details="auto"
																		></v-text-field>
																	</template>
																	<v-date-picker
																		v-model="selectRangeCondition"
																		range
																		@input="menu2 =(selectRangeCondition.length>=2)?false:true"
																	></v-date-picker>
																</v-menu>
															</v-col>
															<v-col cols="1" class='pa-0 ma-0'>
																<v-btn class='mt-8' small @click="addEventData()">
																	<v-icon>mdi-plus</v-icon>
																</v-btn>
															</v-col>
														</v-row>
														
													</v-col>
												</v-row>

												<!-- Single Tag -->
												<v-row  v-if="selectType ==='singleTag'">
													<v-col>

														<!-- Single Tag -->
														<v-row>
															<v-col col="12">

																<v-card style="background: rgba(255, 255, 255, 0.08) !important;">
																	<v-card-title v-if="selectConditionFunction.length === 0">標籤顯示處 Tag Display</v-card-title>
																	<v-text-field
																		label="搜尋"
																		outlined
																		hide-details
																		v-model="singleTagSearch"
																	></v-text-field>
																	<v-card-text v-if="selectConditionFunction.length > 0">
																		<v-chip
																		class="ma-2"
																		label
																		v-for="(list,index) in selectConditionFunction"
																		:key="'single-'+index"
																		close
																		@click:close="removeSelect(index,'singleTag')">
																		
																		<strong>{{ list }}</strong>&nbsp;
																		</v-chip>
																	</v-card-text>
																</v-card>

															</v-col>
														</v-row>

														<v-row>
															<v-col col="12">
																<v-card-text>
																	<v-treeview
																		v-model="selectConditionObject"
																		:items="filterItems"
																		open-on-click
																		selectable
																		return-object
																		item-value='tag'
																		item-text='tag'
																		item-key="id"
																		expand-icon="mdi-chevron-down"
																	>
																	<template v-slot:label="{item}">
																		<div class="v-treeview-node__label">{{translate[item.tag]||item.tag}}</div>
																	</template>
																	</v-treeview>
																	
																</v-card-text>
															</v-col>
														</v-row>
														
													</v-col>
												</v-row>
												<!-- Transaction Product -->
												<v-row  v-if="selectType ==='transactionProduct'">
													<v-col>
														<v-row>
															<v-col col="12">
																<v-card style="background: rgba(255, 255, 255, 0.08) !important;">
																	<v-card-title>標籤顯示處 Tag Display</v-card-title>
																	<v-card-text>
																		<v-chip
																		class="ma-2"
																		label
																		v-for="(list,index) in selectCondition"
																		:key="'product-'+index"
																		close
																		@click:close="removeSelect(index)">
																		
																		<strong>{{ list }}</strong>&nbsp;
																		</v-chip>
																	</v-card-text>
																</v-card>
															</v-col>
														</v-row>
														
														<!-- Transaction Product -->
														<v-row>
															<v-col cols="5">
																<v-select
																	:items="productOptions"
																	item-value='name'
																	item-text='text'
																	label="篩選群集 Select Group"
																	filled
																	v-model="productSelect"
																></v-select>
															</v-col>
															<v-col cols="6">
																
																<v-text-field
																	v-model="productCondition"
																	filled
																	clearable
																	label="Ex: 8527051"
																>
																</v-text-field>
															</v-col>
															
															<v-col cols="1" class='pa-0 ma-0'>
																<v-btn class='mt-8' small @click="addProductData()">
																	<v-icon>mdi-plus</v-icon>
																</v-btn>
															</v-col>
														</v-row>
														
													</v-col>
												</v-row>

												<!-- Filter Selected by Range -->
												<v-row  v-if="selectType ==='rangeSelect'">
													<v-col>
														<v-row>
															<v-col col="12">
																<v-card style="background: rgba(255, 255, 255, 0.08) !important;">
																	<v-card-title>標籤顯示處 Tag Display</v-card-title>
																	<v-card-text>
																		<v-chip
																		class="ma-2"
																		label
																		v-for="(list,index) in selectCondition"
																		:key="'range-'+index"
																		close
																		@click:close="removeSelect(index)">
																		
																		<strong>{{ list }}</strong>&nbsp;
																		</v-chip>
																	</v-card-text>
																</v-card>
															</v-col>
														</v-row>
														
														<!-- Data Selected by Range -->
														<v-row>
															<v-col cols="4">
																<v-select
																	:items="rangeData"
																	item-value='name'
																	item-text='text'
																	label="篩選類別 Type"
																	filled
																	v-model="filterSelect"
																></v-select>
															</v-col>
															<v-col cols="7" v-if="filterSelectType[0].type ==='number' ">
																
																<v-text-field
																	v-model="filterCondition"
																	filled
																	clearable
																	label=">,<,~ (Ex:0~100)"
																>
																<v-icon
																	slot="prepend"
																	color="green"
																	@click="changeNext()"
																	>
																	{{ symbo }}
																	</v-icon>
																</v-text-field>
																	
															</v-col>
															<v-col cols="7" v-else>
																<!-- 日期格式 -->
																<v-menu
																	v-model="menu2"
																	:close-on-content-click="false"
																	:nudge-right="40"
																	transition="scale-transition"
																	offset-y
																	min-width="auto"
																>
																
																	<template v-slot:activator="{ on, attrs }">
																		<v-text-field
																			v-model="filterDateRange"
																			label="時間範圍 Transaction Date's Range"
																			prepend-icon="mdi-calendar"
																			readonly
																			v-bind="attrs"
																			v-on="on"
																			filled
																			hide-details="auto"
																		></v-text-field>
																	</template>
																	<v-date-picker
																		v-model="selectRangeCondition"
																		range
																		@input="menu2 =(selectRangeCondition.length>=2)?false:true"
																	></v-date-picker>
																</v-menu>
															</v-col>
															<v-col cols="1" class='pa-0 ma-0'>
																<v-btn class='mt-8' small @click="addRangeData()">
																	<v-icon>mdi-plus</v-icon>
																</v-btn>
															</v-col>
														</v-row>
														
													</v-col>
												</v-row>
											</v-col>
											<v-col cols="1" align="center">
												<v-btn
													fab
													dark
													color="primary"
													@click="addMore()"
												>
												<v-icon dark>
													mdi-plus
												</v-icon>
												</v-btn>
											</v-col>
										</v-row>
									</v-container>
								</v-col>
							</v-row>
						</v-container>
					</v-form>


					
					
					<v-card-actions>
						<v-row align="center">
							<v-col
								cols="12"
							>
								<div class="text-center">
									<div class="my-2">
										<v-btn block @click="countProcessing = true;search('count')">
											搜尋 Search
										</v-btn>
									</div>
								</div>
							</v-col>
						</v-row>
					</v-card-actions>
				</v-card>
			</v-col>

			<!-- Right Side's Showing Number and Button Group -->
			<v-col md="4" class="my-2">
				<v-row class="d-flex align-start justify-center mb-2">
					<v-col md="12" class="my-2">
						<v-card
							class="mx-auto text-center"
							color="primary"
						>
							<v-card-text>
								<v-progress-circular
								:rotate="-90"
								:size="100"
								:width="15"
								:value="value"
								color="primary"
								v-if='countProcessing'
								>
								{{ value }}
								</v-progress-circular>
								<div v-else>
									<p class='font-weight-black text-h3 pa-5' style="color:#ffbb00"> 
										{{ count | formatNumber }}
									</p>
									<p class="text-right">會員數 The Number of Member</p>
								</div>
								
							</v-card-text>

						</v-card>
						<div class="my-2" style="margin:0px 8px 0px -8px;">
							<v-btn
								block
								:disabled="loading"
								color="blue-grey"
								class="ma-2 white--text"
								@click="downloadCSVData()"
								>
								下載受眾包 Download File
								<v-icon
									right
									dark
								>
									mdi-cloud-upload
								</v-icon><small style='color:#ddd'> 上限:200萬</small>
							</v-btn>
							<v-btn
								block
								:disabled="loading"
								color="cyan darken-4"
								class="ma-2 white--text "
								hide-details
								@click="copy()"
								>
								拷貝文字 Copy String
								<v-icon
									right
									dark>
									mdi-arrange-bring-forward
								</v-icon>
								<small style='color:#ddd'> 上限:依記憶體</small>
							</v-btn>
							<v-btn
								block
								:disabled="loading"
								color="#00c300"
								class="ma-2 white--text "
								hide-details
								@click="getId('line')"
								>
								上傳受眾至Line Upload Audience to Line
								<v-icon
									right
									dark
								>
									mdi-cloud-upload
								</v-icon>
							</v-btn>
							<v-btn
								block
								:disabled="loading"
								color="#0070d2"
								class="ma-2 white--text "
								hide-details
								@click="getId('ideliver')"
								>
								上傳至IDeliver Upload
								<v-icon
									right
									dark
								>
									mdi-cloud-upload
								</v-icon>
								<small style='color:#ddd'> 上限:200萬/2日處理</small>
							</v-btn>
							<v-btn
								block
								:disabled="loading"
								color="#ffbb00"
								class="ma-2 white--text "
								hide-details
								@click="getId('dataStudio')"
								>
								上傳至Data Studio 分析
								<v-icon
									right
									dark
								>
									mdi-account-reactivate 
								</v-icon>
							</v-btn>
						</div>

					</v-col>
				</v-row>
				
				<v-row>
					<v-col>
						<iframe width='455px' height="325" src="https://docs.google.com/presentation/d/1jWSEbGOAUUTPffKwOHQcjU9KZoJ4F_kukgCpiWDlBE8/preview?slide=id.p" title="description"></iframe>
						<v-img
							lazy-src="https://picsum.photos/id/11/10/6"
							max-height="325"
							max-width="455"
							src="https://lh3.google.com/u/0/d/1cagEj9fUpBMY2uU_UvICkanDxehRONS5=w1600-h756-iv1"
						></v-img>
					</v-col>
				</v-row>
				<v-row>
					<v-col>
						<!--<v-img width='455px' height="325" src="https://drive.google.com/file/d/1cagEj9fUpBMY2uU_UvICkanDxehRONS5/view?usp=sharing" title="description"></iframe>-->
					</v-col>
				</v-row>
			</v-col>

			<!-- Warning -->
			<v-dialog
				v-model="warning"
				max-width="500px"
			>

				<v-alert
					color="blue-grey"
					dark
					dense
					icon="mdi-school"
					prominent
				>
				{{ warningContent }}
				</v-alert>
			</v-dialog>

			<!-- Popup Window -->
			<v-overlay :value="overlay">

				<v-progress-circular
					v-if='processing'
					indeterminate
					size="64"
					v-click-outside="()=>{this.overlay=false}"
				></v-progress-circular>
				<v-card
					v-click-outside="()=>{this.overlay=false}"
					v-else
					width='400'
					class="mx-auto text-center"
					>
					<v-card-text>
						<v-text-field
							label="受眾包名稱"
							outlined
							hide-details
							v-model="audienceGroupName"
						></v-text-field>
					</v-card-text>
					<v-card-actions hide-details v-if="destination==='line'">
						<v-container>
						<v-row>
							<v-col md='12'>
								<v-btn
									block
									class="white--text"
									color='red'
									:loading="loading"
									:disabled="loading"
									@click="uploadToLine()">
									開始上傳Line受眾(限1萬)
								</v-btn>
							</v-col>
						</v-row>
						<v-row>
							<v-col md='12'>
								<v-btn
									block
									class="white--text"
									color='red'
									:loading="loading"
									:disabled="loading"
									@click="downloadLineAudience()">
									下載Line受眾包
								</v-btn>
							</v-col>
						</v-row>
						</v-container>
					</v-card-actions>
					<v-card-actions hide-details v-else-if="destination==='ideliver'">
						<v-container>
						<v-row>
							<v-col md='12'>
								<v-btn
									block
									class="white--text"
									color='red'
									:loading="loading"
									:disabled="loading"
									@click="overlay = true;processing = true;search('ideliver')">
									開始上傳IDeliver受眾
								</v-btn>
							</v-col>
						</v-row>
						</v-container>
					
					</v-card-actions>
					<v-card-actions hide-details v-else-if="destination==='dataStudio'">
						<v-container>
						<v-row>
							<v-col md='12'>
								<v-btn
									block
									class="white--text"
									color='red'
									:loading="loading"
									:disabled="loading"
									@click="uploadToDataStudio()">
									開始上傳至DataStudio分析
								</v-btn>
							</v-col>
						</v-row>
						</v-container>
					</v-card-actions>
				</v-card>
			</v-overlay>

		</v-row>
	
	</v-container>
</template>

<script>

	import axios from 'axios';

	export default {
