				}
			],
			rangeData:[
				{
					name:'create_date',
					text:'創建日期 Create Date',
					type:'date'
				},
				{
					name:'first_shop_date',
					text:'第一次消費日期 First Shopping Date',
					type:'date'
				},
				{
					name:'last_shop_date',
					text:'最近消費日期 Last Shopping Date',
					type:'date'
				},
				{
					name:'total_shop_number',
					text:'總購物次數 Total Shopping Count',
					type:'number'
				},
				{
					name:'average_basket_price',
					text:'平均客單價 Average Basket Price',
					type:'number'
				},
				{
					name:'event_count',
					text:'活動參與次數 Event Join Count',
					type:'number'
				},
				{
					name:'coupon',
					text:'抵用卡餘額 Coupon Balance',
					type:'number'
				},
			],
			tags: [
				{
				id: 1,
				tag: '資料撈取中請稍後 Please wait for search data',
				children: []
				}
			],
			list:[
				{ 
					"type": "singleTag",
					"condition": ["district:104 台北市中山區","district:100 台北市中正區","district:106 台北市大安區"] 
				}
			]
		}),
		created(){
			this.getTpye();
			this.getAllAudiences();
		},
		mounted () {
			this.interval = setInterval(() => {
				if (this.value === 100) {
					return (this.value === 0)
				}
				this.value += 10
			}, 1000)
		},
		// Before Finished Updating Remove Alarm 
		beforeDestroy () {
			clearInterval(this.interval)
		},
		methods: {
			
			// uses axios to send get request to api in genericAPI
			getTpye() {

				axios
				// Formal Environment
				.get('https://us-central1-twdatawarehouse-bsv8.cloudfunctions.net/all-tag-detail')
				// Test Environment
				// .get('http://127.0.0.1:3000/tag/all')
				.then((response) => {
					let child = []
					let noOrder = []
					let result = response.data.tag
					Object.keys(result).forEach((key) => {
						let root = result[key]
						let branch = []
						let singleCount = 0
						if(root.length>1) {
							root.forEach((element)=> {
								singleCount += element['count']
								branch.push({
									id:element['tag'] + element['value'],
									tag:element['value'],
									count:element['count'],
									parent:key
								})
							})
							if(typeof(this.orderList[key])!=='undefined'){
								child[this.orderList[key]]={
									id:key,
									tag:key,
									count:singleCount,
									children:branch
								}
							}else {
								noOrder.push({
									id:key,
									tag:key,
									count:singleCount,
									children:branch
								})
							}
							
						}
						else {
							if(typeof(this.orderList[key])!=='undefined'){
								child[this.orderList[key]]={
									id:key,
									tag:key,
									count:singleCount
								}
							}else {
								noOrder.push({
									id:key,
									tag:key,
									count:singleCount
								})
							}
						}
					})
					
					this.tags = child.concat(noOrder)

					this.eventSport = response.data.event
					this.$forceUpdate();
				})
				.catch((error) => console.log(error));

			},
			getAllAudiences() {
				axios
				// Formal Environment
				.get('https://us-central1-twdatawarehouse-bsv8.cloudfunctions.net/audience-list')
				// Test Environment
				// .get('http://127.0.0.1:3000/audience/list')
				.then((response) => {
					this.audienceList = response.data['audience']
					this.analystAudience = response.data['analystAudience']
				})
				.catch((error) => console.log(error));

			},

			/******** Main Function for Getting All of Audience's member loyalty_card_number, Count, Upload Audience to IDeliver or DataStudio  *******/
			async search(type ='count') {
				let where = []
				this.list.forEach(item=>{
					if(item.condition.length > 0) {
						switch(item.type) {
							case 'transactionDate':
								where.push('transactionDate='+item.condition[0]+','+item.condition[1])
								break;
							case 'eventRelative':
								where.push('eventRelative='+item.condition.join(','))
								break;
							case "rangeSelect":
								where.push('rangeSelect='+item.condition.join(','))
								break;
							case "singleTag":
								where.push("singleTag="+item.condition.join(','))
								break;
							case "transactionProduct":
								where.push("transactionProduct="+item.condition.join(','))
								break;
							default:
								break;

						}
					}
					
				})

				let condition = where.join("|");
				let config = {
					// Formal Environment
					url: 'https://audiences2-xecnibzemq-uc.a.run.app?where='+condition+'&datatype='+type
					// Test Environment
					// url:'http://127.0.0.1:3000/tag/condition?where='+condition+'&datatype='+type 
				}
				config.url+=(type==='ideliver'|| type==='data_studio')?'&name='+this.audienceGroupName:''

				await axios
				.get(config.url)
				.then((response) => {

					switch(type){
						case 'count':
							this.count = response.data.count
						break;
						case 'line':
							this.lineId = response.data
							this.count = this.lineId.length
							break;
						case 'card_number':
							this.member = response.data.map(item=>
							{
								return {
									'loyalty_card_num':item
								}
							})
							this.downloadMember = response.data

							break;
						case 'ideliver':
							this.getAllAudiences();
							if(response.data.uploadResult) {
								this.warning = true
								this.warningContent = '上傳成功'
							}else {
								this.warning = true
								this.warningContent = '上傳失敗請聯繫工程師'
							}
							
							this.count = response.data.count
							this.processing = false
							this.overlay = false
							break;
						case 'data_studio':
							this.getAllAudiences();
							if(response.data.uploadResult) {
								this.warning = true
								this.warningContent = '上傳成功'
							}else {
								this.warning = true
								this.warningContent = '上傳失敗請聯繫工程師'
							}
							
							this.count = response.data.count
							this.processing = false
							this.overlay = false
							break;

					}
					this.countProcessing = false
					this.processing = false
					return true
				})
				.catch((error) =>{ 
					console.log('errortest',error)
					return false
				});
			},
			
			/**
			 * Button Function : 
			 * 		downloadCSVData()
			 * 		copy()
			 * 		getId()
			 * 		downloadLineAudience()
			 * 		uploadToLine()
			 * 		uploadToDataStudio()
			 */
			// Download Audience's Member Card Number to CSV File 
			async downloadCSVData(){
				
				if(this.count>200*10000) {
					this.warning = true
					this.warningContent = '受眾包產生不可大於200萬'
				}else {
					this.overlay = true
					this.processing = true
					this.countProcessing = false

					await new Promise((resolve) => {
						this.search('card_number').then(()=> {
							resolve();
						})
					})	
					let csv = '';
					this.downloadMember.forEach((row) => {
							csv += row+"\n"
					});
					const anchor = document.createElement('a');
					anchor.href = 'data:text/csv;charset=utf-8,' + encodeURIComponent(csv);
					anchor.target = '_blank';
					anchor.download = 'audience.csv';
					anchor.click();
					this.overlay = false
				}
				
			},
			// Copu Audience's Member Card Number 
			async copy() {
				if(this.count>200*10000) {
					this.warning = true
					this.warningContent = '受眾包產生不可大於200萬'
				}else {
					this.overlay = true
					this.processing = true
					/* 會員交易資料處理 */
					await new Promise((resolve) => {
						this.search('card_number').then(()=> {
							resolve();
						})
					})	
					navigator.clipboard.writeText(this.downloadMember).then(function() {
						console.log('Async: Copying to clipboard was successful!');
					}, function(err) {
						this.warning = true
						this.warningContent = '記憶體上限!!!拷貝失敗'
						console.error('Async: Could not copy text: ', err);
					});
					this.overlay = false
				}
			},
			// Calculate How many Member In this Audience 
			async getId(destination) {
				if(this.count>200*10000) {
					this.warning = true
					this.warningContent = '受眾包產生不可大於200萬'
				}else {
					this.destination = destination
					this.overlay = true
					this.processing = false
					await new Promise((resolve) => {
						this.search('count').then(() => {
							resolve();
						})
					})	
					
				}
			},
			async downloadLineAudience(){

					this.overlay = true
					this.processing = true
					await new Promise((resolve) => {
						this.search('line').then(()=> {
							resolve();
						})
					})	
					let csv = '';
					this.lineId.forEach((row) => {
							csv += row+"\n"
					});
					const anchor = document.createElement('a');
					anchor.href = 'data:text/csv;charset=utf-8,' + encodeURIComponent(csv);
					anchor.target = '_blank';
					anchor.download = this.audienceGroupName+'.csv';
					anchor.click();
					this.overlay = false
					this.processing = false
				
			},
			async uploadToLine() {
				
				this.overlay = true
				this.processing = true
				var config = {
					method: 'post',
					// Formal Environment
					url: 'https://us-central1-twdatawarehouse-bsv8.cloudfunctions.net/line-audience-upload',
					// Test Environment
					// url: 'http://127.0.0.1:3000/line',
					headers: { 
						'Content-Type': 'application/json', 
						'Access-Control-Allow-Origin': '*'
					},
					data : {
						'audiencesName':this.audienceGroupName,
						'lineId':this.lineId,
					}
				};

				await axios(config)
				.then((response) => {
					this.loading = false
					this.overlay = false
					this.warning = true
					this.warningContent = (response.data)?'上傳成功':'上傳失敗'
				})
				.catch(function (error) {
					console.log(error);
				});

			},
			async uploadToDataStudio() {

				this.overlay = true
				this.processing = true
				await new Promise((resolve) => {
					this.search('data_studio').then(()=> {
						resolve();
					})
				})	

			},

			/**
			 * Adjustable Small Function : 
			 * 		addEventData()
			 * 		addProductData()
			 * 		addRangeData()
			 * 		remove()
			 * 		changeNext()
			 * 		addMore()
			 * 		removeCondition()
			 * 		removeSelect()
			 */
			addEventData() {
				if(this.eventDetailType[0].type === 'select') {
					this.selectCondition.push(this.eventTypeSelect+':'+this.filterCondition)
					this.filterCondition = null
				}else {
					this.selectCondition.push(this.eventTypeSelect+':'+this.filterDateRange)
					this.filterSelect=''
					this.filterCondition=''
				}

			},
			addProductData() {
				this.selectCondition.push(this.productSelect+':'+this.productCondition)
				this.productCondition = null
			},
			addRangeData() {
				if(this.filterSelectType[0].type === 'number') {
					if(this.symbo==='~'){
						this.selectCondition.push(this.filterSelect+' '+this.filterCondition)
					}else {
						this.selectCondition.push(this.filterSelect+' '+this.symbo+this.filterCondition)
					}
				}else {
					this.selectCondition.push(this.filterSelect+' '+this.filterDateRange)
					this.filterSelect=''
				}
				this.filterCondition=''
			},
			remove (index,key) {
				this.list[key]['condition'].splice(index, 1)
			},
			changeNext:function() {
				let check = this.symbo
				var finalIndex = 0
				this.symboList.forEach((value, index) => {
				
					if(value === check) {
						finalIndex = index +1
					}
				});
				this.symbo = this.symboList[finalIndex%3]
			},
			addMore:function() {
				if(this.selectType ==='singleTag') {
					this.list.push({
						type:this.selectType,
						condition:this.selectConditionFunction,
					})
				}else {
					this.list.push({
						type:this.selectType,
						condition:this.selectCondition,
					})
				}
				this.selectType = 'transactionDate'
				this.selectCondition = []
				this.selectConditionObject = []
			},
			removeCondition:function(key){
				this.list.splice(key, 1)
			},
			removeSelect:function(key,type=null) {
				if(type ==='singleTag') {
					this.selectConditionObject.splice(key,1)
				}else {
					this.selectCondition.splice(key,1)
				}
			}
		},
		watch: {
			loader () {
				const l = this.loader
				this[l] = !this[l]
				setTimeout(() => (this[l] = false), 3000)
				this.loader = null
			},
			selectType:function() {
				this.selectCondition = []
				this.selectConditionObject = []
			}
		},
		computed: {
			filterSelectType() {
				let result =  this.rangeData.filter(item=> {
					if(item.name === this.filterSelect) {
						return true
					}
				})
				return result
			},
			eventDetailType() {
				let result =  this.eventType.filter(item=> {
					if(item.name === this.eventTypeSelect) {
						return true
					}
				})
				return result
			},
			dateRangeText() {
				// this.selectCondition.sort();
				return this.selectCondition.join(" ~ ");
			},
			filterDateRange() {
				// this.selectRangeCondition.sort();
				return this.selectRangeCondition.join("~");
			}, 
			selectConditionFunction(){
				let result =this.selectConditionObject.map(item=>{

					if(typeof item.parent !== 'undefined'){
						return item.parent+':'+item.tag
					}else {
						return item.tag
					}

				})
				return result
			},

			filterItems(){
				
				let search = this.singleTagSearch.toString().toLowerCase()
				
				if(search === '') {
					return this.tags
				}else {
					let result = []
					
					this.tags.forEach((rootItem)=>{

						if(rootItem['tag'].toString().toLowerCase().includes(search)){
							result.push(rootItem)
						}else {
							if(typeof rootItem['children'] !='undefined' ) {
								rootItem['children'].forEach((element)=>{
									if(element['tag'].toString().toLowerCase().includes(search)){
										result.push(element)
									}
								})	
							}
						}
						
					})
					return result					
				}

			}
		},
	}
</script>

