import Vue from "vue";
import "./components"; //this is where the auto registered components import
import "./plugins";
// Application imports
import App from "./App";
import i18n from "@/i18n";
// import router from "@/router";
// import store from "@/store";
import { genericApi } from "./plugins/axios";
import vuetify from "./plugins/vuetify";
// import VueWordCloud from 'vuewordcloud';
import VueCookies from "vue-cookies";

// makes genericApi common base instance axios the default http handler
Vue.prototype.$http = genericApi;

Vue.use(vuetify);
Vue.use(VueCookies, { expire: "30d" });

// Sync store with router
// sync(store, router);

Vue.config.productionTip = false;
// Vue.component(VueWordCloud.name, VueWordCloud);
Vue.filter("formatNumber", (value) => {
  return Number(parseFloat(value).toFixed(3)).toLocaleString("en", {
    minimumFractionDigits: 0,
  });
});

/* eslint-disable no-new */
new Vue({
  i18n,
  // router,
  VueCookies,
  // store,
  vuetify,
  render: (h) => h(App),
}).$mount("#app");
